@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
.modal-todo-component-content .color-palette svg {
  margin-right: 12px;
  font-size: 1.3rem;
}

.modal-todo-component-content .input-date-time {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 8px;
  gap: 8px;
  justify-content: space-between;
}

.modal-todo-component-content .input-date-time .ant-form-item {
  width: 48%;
}

.modal-todo-component-content.ant-form label {
  width: 40px;
  height: 40px;
  border-radius: 4px;
}


.todo-item-component-content {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 8px;
  border-radius: 8px;
  background: #fff;
}

.todo-item-component-content .col-1 {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  min-height: 50px;
}

.todo-item-component-content .col-1 .description {
  margin: 0 8px 12px 0;
  font-weight: 500;
  flex: 1 1;
}

.todo-item-component-content .col-1 .date-time {
  display: flex;
  flex-direction: row;
}

.todo-item-component-content .col-1 .date-time .date-expired {
  color: var(--color-error);
}

.todo-item-component-content .col-1 svg {
  margin-right: 4px;
}
.todo-item-component-content .col-1 .date,
.todo-item-component-content .col-1 .time {
  margin-right: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.todo-item-component-content .col-2 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.todo-item-component-content .col-2 svg {
  font-size: 1.1rem;
  transition: .5s;
}

.todo-item-component-content .col-2 svg:hover {
  cursor: pointer;
  color: var(--color-secondary);
}

@media(min-width: 601px) {
  .todo-item-component-content {
    width: calc(98%/3);
  }
}
.todo-list-component-content {
  display: flex;
  flex-direction: column;  
  justify-content: center;
  align-items: center;
}

.todo-list-component-content .new-todo-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 64px;
}

.todo-list-component-content .new-todo-content > span {
  font-size: 1.3rem;
  width: 60%;
}

.todo-list-component-content .menu {
  width: 100%;
  height: 32px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 16px 0;
}

.todo-list-component-content .menu span {
  padding: 4px 12px;
  transition: color .5s;

}

span.menu-selected {
  font-weight: 700;
  color: var(--color-secondary);
  border: 1px solid var(--color-secondary);
  border-radius: 16px;
}

.todo-list-component-content .menu span:hover {
  cursor: pointer;
  color: var(--color-secondary);
}

.todo-list-component-content .list {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 8px;
  gap: 8px;
  width: 100%;
  max-height: calc(100vh - 200px);
  overflow: auto;
}

.todo-list-component-content .list-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

@media(min-width: 601px) {
  .todo-list-component-content .menu{
    width: 40vw;
  }
}
.user-avatar-component-content svg {
  font-size: 1.8rem;
}
.header-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  height: 35px;
}

@media (min-width: 601px) {
  .header-content {
    justify-content: flex-end;
  }

  .header-content > span {
    margin-right: 12px;
  }
}
:root {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --color-background: #fff;

  --color-primary: #264653;
  --color-secondary: #1890ff;

  --color-text-primary: #264653;
  --color-text-secondary: #000;
  --color-text-tertiary: #fff;

  --color-error: #ec1111;
  --color-success: #0fc000;
  --color-warning: #e9ee09;
}

body {
  height: 100vh;
  color: #264653;
  color: var(--color-text-primary);
  padding: 8px;
}

::-webkit-scrollbar {
  width: 5px;
  background: #fff;
  background: var(--color-background);
}

::-webkit-scrollbar-thumb {
  background: #1890ff;
  background: var(--color-secondary); 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #1890ff;
  background: var(--color-secondary); 
}

.app-content {
  display: flex;
  flex-direction: column;
}


